<template>
  <div class="content">
    <div class="container-top-wrapper">
      <div class="container-top">
        <div class="b-l">
          <Banner></Banner>
        </div>
        <div class="b-r">
          <BContentTop></BContentTop>
        </div>
      </div>
    </div>
    <!-- 推广 -->
    <!-- <div class="container-row">
			<BPromote></BPromote>
		</div> -->
    <!-- 直播 -->
    <!-- <div>{{$router}}</div> -->
    <div class="container-row">
      <BLive></BLive>
    </div>
    <!-- 各分类具体内容 -->
    <div
      class="container-row"
      v-for="(row, index) in rows"
      :id="row.b_id"
      :key="index"
      :v-if="rows"
    >
      <BContentRow
        :category="row.category"
        :categoryId="row.categoryId"
        :row="row.item"
        :name="row.name"
        :key="componentKey"
        :index="index"
      ></BContentRow>
    </div>
  </div>
</template>

<script>
import Banner from "components/banner/Banner";
import BContentTop from "components/contentTop/BContentTop";
import BPromote from "components/promote/BPromote";
import BLive from "components/live/BLive";
import BContentRow from "components/contentRow/BContentRow";
import { regionApi } from "api/index";
import { regionTags } from "api/config";
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      rows: [],
      index: 1,
      regionTags: regionTags,
      componentKey:0
    };
  },
  create() {
    this.getrouterindex();

    this.getrows();
  },
  mounted() {
    this.getrouterindex();

    this.getrows();
  },
  methods: {
    getrouterindex() {
      this.index = this.$route.params.index;
    },
    forceRerender() {
    //   this.componentKey += 1;  
    },
    getrows() {
      let rid = regionTags[this.index].key;
      let blocks = regionTags[this.index].blocks;
      this.rows=[]  
      for (let i = 1; i < blocks.length; i++) {
        let block = blocks[i];
        let category = block.sortKeys;
        regionApi.getRegion(15, block.key).then((response) => {
        let resp = response.data;
        let rowItem = {
        category: category,
        categoryId: block.key,
        name: block.name,
        b_id: `b_${category}`,
        item: Object.values(resp["archives"]),
        };
        this.rows.push(rowItem);
        }, (error) => {
        console.log(error)
		});

      }
    },

  },
    watch: {
    '$route' () {
      let routeIndex = this.$route.params.index
      if (this.indexTab !== index) {
        this.getrouterindex()
        this.regionList = []
        setTimeout(() => {
          this.getrows()
          this.forceRerender()
        //   console.log(this.componentKey)
        }, 20)
      }
    }
  },
  props: {
    // rows: {
    // 	type: Array
    // }
  },
  components: {
    Banner,
    BContentTop,
    BPromote,
    BLive,
    BContentRow,
  },
};
</script>

<style lang="stylus" scoped>
.content {
  zoom: 1;

  .container-top-wrapper {
    &:after {
      content: '';
      display: block;
      visibility: hidden;
      height: 0;
      clear: both;
      font-size: 0;
    }
  }

  .container-top {
    padding: 0;
    margin: 0 auto;
    margin-bottom: 20px;
    zoom: 1;
    width: 980px;

    &:after {
      content: '';
      display: block;
      visibility: hidden;
      height: 0;
      clear: both;
      font-size: 0;
    }

    .b-l {
      float: left;
      width: 440px;
    }

    .b-r {
      float: right;
      width: 540px;
      height: auto;
    }
  }

  .container-row {
    margin: 0 auto;
    zoom: 1;
    width: 980px;

    &:after {
      content: '';
      display: block;
      visibility: hidden;
      height: 0;
      clear: both;
      font-size: 0;
    }
  }
}
</style>


